@font-face {
  src: local("Beyond The Mountains"),
    url("beyond_the_mountains.woff") format("woff");
  font-family: "Beyond The Mountains";
  font-style: normal;
  font-weight: normal;
}

@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@1,300&display=swap");

html {
  height: 100%;
  overflow: auto;
}

body {
  margin: 0;
  background-repeat: no-repeat;
  background-attachment: fixed;
  min-height: 2000px;
  background-color: #fff;
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}

::-webkit-scrollbar {
  display: none;
}

.navbar {
  margin: 1rem 1rem;
  background-color: rgba(230, 230, 230, 0.733);
  border-radius: 1rem;
  padding: 0;
}

.navbar-light .navbar-nav .nav-link {
  color: rgb(95, 95, 95);
  font-family: "Montserrat", sans-serif;
  font-size: 1rem;
}

.navbar-nav.me-auto {
  margin: auto;
}

.navbar .navbar-toggler {
  margin-left: auto;
  margin-right: 0;
  border-radius: .5rem;
}

.navbar-toggler:focus,
.navbar-toggler:active,
.navbar-toggler-icon:focus,
.navbar-toggler:hover
 {
  color: transparent;
  background-color: rgba(255, 255, 255, 0.1) !important;

}

a.nav-link:hover {
  color: rgb(0, 0, 0);
  font-weight: bold;
}

.menu {
  background-color: transparent;
  border: none;
  cursor: pointer;
  display: flex;
  padding: 0;
}

.line {
  fill: none;
  stroke: black;
  stroke-width: 4;
  transition: stroke-dasharray 600ms cubic-bezier(0.4, 0, 0.2, 1),
    stroke-dashoffset 600ms cubic-bezier(0.4, 0, 0.2, 1);
}

.line1 {
  stroke-dasharray: 60 207;
  stroke-width: 4;
}

.line2 {
  stroke-dasharray: 60 60;
  stroke-width: 4;
}

.line3 {
  stroke-dasharray: 60 207;
  stroke-width: 4;
}

.opened .line1 {
  stroke-dasharray: 90 207;
  stroke-dashoffset: -134;
  stroke-width: 4;
}

.opened .line2 {
  stroke-dasharray: 1 60;
  stroke-dashoffset: -30;
  stroke-width: 4;
}

.opened .line3 {
  stroke-dasharray: 90 207;
  stroke-dashoffset: -134;
  stroke-width: 4;
}

#home {
  height: 100vh;
  width: 100vw;
  padding: 0;
  background-color: rgb(49, 49, 49);
}

.title-div {
  height: 100%;
  width: 100%;
  text-align: center;
  border-radius: 0 0 40px 0;
  background-image: linear-gradient(#68e0cf, #20bdff);
}

.wingit {
  font-family: "Beyond The Mountains";
  font-weight: normal;
  color: #fff;
  text-shadow: 5px 5px 0px rgba(0, 0, 0, 0.1), 7px 7px 0px rgba(0, 0, 0, 0.05);
}

.title-div .wingit-title {
  position: relative;
  top: 35%;
}

.title-div .wingit {
  font-size: 6rem;
}

h1.before-typing {
  padding-top: 30px;
  position: relative;
  color: #fff;
}

.title-animation {
  position: relative;
  top: 53%;
  left: 50%;
  width: 360px;
  transform: translateX(-50%);
  text-align: left;
}

a:link,
a:visited {
  text-decoration: none;
  color: #fff;
}

/* Scroll down icon animamtion */
.container.scroll {
  position: absolute;
  left: 50%;
  top: 85vh;
  min-width: fit-content;
  transform: translateX(-50%);
  width: 24px;
  height: 24px;
  text-align: center;
}

.chevron {
  position: absolute;
  left: 35%;
  width: 28px;
  height: 8px;
  opacity: 0;
  transform: scale3d(0.5, 0.5, 0.5);
  animation: move 3s ease-out infinite;
}

.chevron:first-child {
  animation: move 3s ease-out 1s infinite;
}

.chevron:nth-child(2) {
  animation: move 3s ease-out 2s infinite;
}

.chevron:before,
.chevron:after {
  content: " ";
  position: absolute;
  top: 0;
  height: 100%;
  width: 51%;
  background: #fff;
}

.chevron:before {
  left: 0;
  transform: skew(0deg, 30deg);
}

.chevron:after {
  right: 0;
  width: 50%;
  transform: skew(0deg, -30deg);
}

@keyframes move {
  25% {
    opacity: 1;
  }
  33% {
    opacity: 1;
    transform: translateY(30px);
  }
  67% {
    opacity: 1;
    transform: translateY(40px);
  }
  100% {
    opacity: 0;
    transform: translateY(55px) scale3d(0.5, 0.5, 0.5);
  }
}

.text {
  display: block;
  position: relative;
  top: 75px;
  font-family: "Helvetica Neue", "Helvetica", Arial, sans-serif;
  font-size: 12px;
  color: #fff;
  text-transform: uppercase;
  white-space: nowrap;
  opacity: 0.25;
  animation: pulse 2s linear alternate infinite;
}

@keyframes pulse {
  to {
    opacity: 1;
  }
}

/* About section */
#about {
  position: relative;
  /* min-height: 200vh; */
  width: 100vw;
  padding: 0;
  background-image: linear-gradient(#20bdff, #f089e5);
}

.about-div {
  text-align: center;
  align-items: center;

  width: 100%;
  background-color: rgb(49, 49, 49);
  border-radius: 40px 0 40px 0;
}

/* transition background-color of pushed class when card is hovered */
.pushed {
  background-color: rgb(49, 49, 49);
  box-shadow: 8px 8px 12px 0 rgba(0, 0, 0, 0.15),
    -6px -6px 10px 0 rgba(255, 255, 255, 0.1);
  border-radius: 50px;
  /* display: flex; */
}

.card:hover.pushed {
  background: linear-gradient(
    -45deg,
    rgba(0, 0, 0, 0.2),
    rgba(255, 255, 255, 0.068)
  );
  box-shadow: 10px 10px 14px 0 rgba(0, 0, 0, 0.15),
    -8px -8px 12px 0 rgba(255, 255, 255, 0.1);
}

.about-div .section-title {
  /* font-family: 'Comfortaa', cursive; */
  font-family: "Montserrat", sans-serif;
  padding-top: 2rem;
  font-size: 2.5rem;
  color: #fff;
}

.cards {
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 0);
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-auto-rows: auto;
  grid-gap: 2rem;
  width: 80%;
  padding-top: 2rem;
  padding-bottom: 4rem;
  height: 100%;
}

.card {
  min-height: 15rem;
}

.card-heading {
  font-size: 2rem;
  letter-spacing: 0.5px;
  text-align: left;
  padding-top: 3rem;
  padding-left: 2rem;
  background-image: linear-gradient(128deg, #20bdff, #f089e5 50%, #ffc444);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.card-text {
  font-size: 1rem;
  font-weight: normal;
  color: #fff;
  letter-spacing: 0.5px;
  text-align: left;
  padding: 1rem 2rem 2rem 2rem;
}

/* Plans section */
#plans {
  min-height: 100vh;
  width: 100vw;
  padding: 0;
  background-color: rgb(49, 49, 49);
}

.plans-div {
  min-height: 100vh;
  width: 100%;
  background-image: linear-gradient(#f089e5, #ec8b6b);
  border-radius: 40px 0 40px 0;
}

/* Contact section */
#contact {
  position: relative;
  height: fit-content;
  width: 100vw;
  padding: 0;
  background-image: linear-gradient(#ec8b6b, #68e0cf);
}

.contact-div {
  height: 100%;
  width: 100%;
  background-color: rgb(49, 49, 49);
  border-radius: 40px 0 40px 0;
}

.contact-form-div {
  margin: auto;
  max-width: 80rem;
}

.contact-form {
  overflow-x: hidden;
  display: grid;
  grid-template-columns: 70% 30%;
  grid-auto-rows: auto;
  grid-gap: 4rem;
  padding: 2rem 2rem 7rem 2rem;
  background-color: rgb(49, 49, 49);
  border-radius: 40px;
  /* display: flex; */
}

.pressed {
  box-shadow: 12px 12px 24px 0 rgba(0, 0, 0, 0.2),
    -12px -12px 24px 0 rgba(255, 255, 255, 0.5);
  box-shadow: inset 6px 6px 10px 0 rgba(0, 0, 0, 0.2),
    inset -6px -6px 10px 0 rgba(255, 255, 255, 0.2);
  font-size: 5rem;
  border-radius: 40px;
  overflow: hidden;
  padding: 1.3rem;
  display: flex;
}

.contact-title {
  font-size: 5rem;
  letter-spacing: 0.5px;
  text-align: left;
  padding-top: 7rem;
  padding-left: 2rem;
  color: #fff;
}

.contact-text {
  font-size: 1rem;
  font-weight: normal;
  color: #fff;
  letter-spacing: 0.5px;
  text-align: left;
  padding: 0 2rem 0 2rem;
}

.contact-form {
  display: grid;
  grid-template-columns: auto;
  grid-auto-rows: auto;
  grid-gap: 4rem;
  padding: 1.5rem 2rem 7.5rem 2rem;
  background-color: rgb(49, 49, 49);
  border-radius: 40px;
  color: #fff;
  /* display: flex; */
}

.email {
  color: #fff;
  height: 5rem;
  width: 60vw;
  min-width: 19rem;
  max-width: 50rem;
  text-align: left;
  padding-top: 1.5rem;
  padding-left: 2rem;
  background-color: #373737;
  border: 0;
  font-size: 2rem;
  overflow: scroll; /* font-weight: bold; */
}

/* change color of typing text inside email input */
.email:focus {
  color: #fff;
  outline: none;
}

.email:placeholder-shown {
  font-size: 2rem;
  font-weight: bold;
  letter-spacing: 0.5px;
  background-image: linear-gradient(128deg, #20bdff, #f089e5 30%, #ffc444);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.submit {
  text-align: center;
  height: 5rem;
  width: 20vw;
  font-size: 2rem;
  font-weight: bold;
  letter-spacing: 0.5px;
  background-image: linear-gradient(128deg, #20bdff, #f089e5 50%, #ffc444);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  min-width: fit-content;
  max-width: 15rem;
  border: none;
  padding: 1rem;
}

/* Footer section */
#footer {
  position: relative;
  height: 70vh;
  width: 100vw;
  padding: 0;
  background-color: rgb(49, 49, 49);
}

.footer-div {
  height: 100%;
  width: 100%;
  background-image: linear-gradient(#68e0cf, #20bdff);
  border-radius: 40px 0 0 0;
}

.footer-bar {
  position: absolute;
  bottom: 0;
  height: 6rem;
  width: calc(100vw - 2rem);
  min-width: 8rem;
  background-color: rgba(230, 230, 230, 0.733);
  border-radius: 1rem;
  margin: 0 1rem 1rem 1rem;
  text-align: center;
}

.footer-bar a.wingit {
  position: relative;
  top: 40%;
  font-size: 1rem;
  text-shadow: 3px 3px 0px rgba(0, 0, 0, 0.1), 4px 4px 4px rgba(0, 0, 0, 0.05);
}

/* Media queries */
@media screen and (min-width: 500px) {
  .navbar {
    padding: 0.5rem;
  }
  .title-div {
    border-radius: 0 0 40px 0;
  }

  .title-div .wingit {
    font-size: 8rem;
  }

  .about-div {
    width: 100%;
    background-color: rgb(49, 49, 49);
  }
  .about-div .section-title {
    /* font-family: 'Comfortaa', cursive; */
    font-family: "Montserrat", sans-serif;
    padding-top: 3rem;
    font-size: 3rem;
    color: #fff;
  }
  .cards {
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, 0);
    width: 80%;
  }

  .about-div,
  .plans-div,
  .contact-div {
    border-radius: 40px 0 40px 0;
  }

  .footer-div {
    border-radius: 40px 0 0 0;
  }

  .footer-bar {
    height: 8rem;
    width: calc(100vw - 4rem);
    margin: 0 2rem 2rem 2rem;
  }

  .footer-bar a.wingit {
    top: 35%;
    font-size: 2rem;
    text-shadow: 4px 4px 0px rgba(0, 0, 0, 0.1), 5px 5px 5px rgba(0, 0, 0, 0.05);
    /* transform: translateY(-50%); */
  }
}

@media screen and (min-width: 992px) {
  .navbar {
    padding: 1rem;
  }

  .title-div {
    border-radius: 0 0 40px 0;
  }
  .title-div .wingit-title {
    top: 17%;
  }

  .title-div .wingit {
    font-size: 15rem;
  }

  h1.before-typing {
    padding-top: 60px;
  }
  .about-div {
    height: 100%;
    width: 100%;
    background-color: rgb(49, 49, 49);
  }

  .about-div .section-title {
    /* font-family: 'Comfortaa', cursive; */
    font-family: "Montserrat", sans-serif;
    padding: 3rem 0 1rem 0;
    font-size: 4rem;
    color: #fff;
  }
  .cards {
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, 0);
    width: 80%;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    grid-gap: 2rem;
  }

  .about-div,
  .plans-div,
  .contact-div {
    border-radius: 40px 0 40px 0;
  }

  .contact-form {
    overflow-x: hidden;
    display: grid;
    grid-template-columns: 70% 30%;
    grid-auto-rows: auto;
    grid-gap: 4rem;
    padding: 2rem 2rem 7rem 2rem;
    background-color: rgb(49, 49, 49);
    border-radius: 40px;
    /* display: flex; */
  }

  .footer-div {
    border-radius: 40px 0 0 0;
  }

  .footer-bar {
    height: 8rem;
    width: calc(100vw - 4rem);
    margin: 0 2rem 2rem 2rem;
  }

  .footer-bar a.wingit {
    top: 35%;
    font-size: 2rem;
    text-shadow: 4px 4px 0px rgba(0, 0, 0, 0.1), 5px 5px 5px rgba(0, 0, 0, 0.05);
  }
}
