  }

  .title-div .wingit {
    font-size: 15rem;
  }

  h1.before-typing {
    padding-top: 60px;
  }
  .about-div {
    height: 100%;
    width: 100%;
    background-color: rgb(49, 49, 49);
  }

  .about-div .section-title {
    /* font-family: 'Comfortaa', cursive; */
    font-family: "Montserrat", sans-serif;
    padding: 3rem 0 1rem 0;
    font-size: 4rem;
    color: #fff;
  }
  .cards {
    position: relative;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
    width: 80%;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    grid-gap: 2rem;
  }

  .about-div,
  .plans-div,
  .contact-div {
    border-radius: 40px 0 40px 0;
  }

  .contact-form {
    overflow-x: hidden;
    display: grid;
    grid-template-columns: 70% 30%;
    grid-auto-rows: auto;
    grid-gap: 4rem;
    padding: 2rem 2rem 7rem 2rem;
    background-color: rgb(49, 49, 49);
    border-radius: 40px;
    /* display: flex; */
  }

  .footer-div {
    border-radius: 40px 0 0 0;
  }

  .footer-bar {
    height: 8rem;
    width: calc(100vw - 4rem);
    margin: 0 2rem 2rem 2rem;
  }

  .footer-bar a.wingit {
    top: 35%;
    font-size: 2rem;
    text-shadow: 4px 4px 0px rgba(0, 0, 0, 0.1), 5px 5px 5px rgba(0, 0, 0, 0.05);
  }
}

